@use "base/variables" as v;
@use "base/mixins" as m;

.navbar {
  width: 100%;
  &__links {
    width: inherit;
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
  }
  &__link {
    width: 100%;
    text-align: center;
    margin: 0.2rem 0;
    a {
      display: inline-block;
      width: 100%;
      color: v.$blanco;
      padding: 1rem;
      transition: all 0.3s ease;
      &:hover {
        margin: 0;
        background-color: v.$links;
      }
    }
  }
}

.selected {
  padding: 1rem;
  background-color: v.$links;
}
