@use "variables" as v;

html {
  font-size: 62.5%;
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  font-family: v.$fuentePrincipal;
  font-size: 1.6rem;
  position: relative;
}
h1,
h2,
h3 {
  font-family: v.$fuenteTitulo;
}
h1 {
  font-size: 3.5rem;
}
h2 {
  font-size: 2.8rem;
}
h3 {
  font-size: 2rem;
}
p {
  color: v.$negro;
  font-size: 1.6rem;
  line-height: 2;
}
.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}
a {
  text-decoration: none;
  color: v.$negro;
}
ul {
  list-style: none;
}
img {
  max-width: 100%;
  height: auto;
  display: block;
}
.color-degrade {
  color: transparent;
  background: linear-gradient(to right, v.$linkHover 0%, v.$links 100%);
  -webkit-background-clip: text;
  background-clip: text;
}
