@use "base/variables" as v;
@use "base/mixins" as m;

.skills {
  @include m.desktop {
    margin-left: 25rem;
  }
  &__header {
    background-image: url(../img/header-about.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 20rem;
    margin-bottom: 1rem;
  }
  &__heading {
    width: 30rem;
    border-bottom: solid 0.3rem v.$secundario;
    color: v.$secundario;
  }
  &__content {
    @include m.tablet {
      @include m.grid(2, 0rem);
    }
  }
}
