html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Red Hat Display", sans-serif;
  font-size: 1.6rem;
  position: relative;
}

h1,
h2,
h3 {
  font-family: "IBM Plex Sans", sans-serif;
}

h1 {
  font-size: 3.5rem;
}

h2 {
  font-size: 2.8rem;
}

h3 {
  font-size: 2rem;
}

p {
  color: #121212;
  font-size: 1.6rem;
  line-height: 2;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #121212;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.color-degrade {
  color: transparent;
  background: linear-gradient(to right, #005e7c 0%, #0094c6 100%);
  -webkit-background-clip: text;
  background-clip: text;
}

h1,
h2,
h3 {
  font-family: "IBM Plex Sans", sans-serif;
  margin: 0 0 5rem 0;
  text-align: center;
  line-height: 1.5;
}

.sidebar {
  position: fixed;
  width: 25rem;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  background-image: linear-gradient(to left bottom, #000022, #03052a, #030a32, #010f3a, #001242);
  top: 0;
  left: 0;
  transform: translateX(-100%);
  transition: transform 0.3s ease;
}
@media (min-width: 1024px) {
  .sidebar {
    transform: translateX(0);
  }
}
.sidebar__header {
  padding-top: 2rem;
}
.sidebar__logo {
  border-radius: 50%;
  margin-bottom: 2rem;
}
.sidebar__slogan {
  text-align: center;
  font-family: "Permanent Marker", cursive;
  font-size: 2rem;
  color: #fff;
  margin: 0;
}
.sidebar__footer {
  width: 100%;
  padding: 1rem 0;
  margin: 1rem 0;
}

.open {
  transform: translateX(0);
}

.navbar {
  width: 100%;
}
.navbar__links {
  width: inherit;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
}
.navbar__link {
  width: 100%;
  text-align: center;
  margin: 0.2rem 0;
}
.navbar__link a {
  display: inline-block;
  width: 100%;
  color: #fff;
  padding: 1rem;
  transition: all 0.3s ease;
}
.navbar__link a:hover {
  margin: 0;
  background-color: #0094c6;
}

.selected {
  padding: 1rem;
  background-color: #0094c6;
}

.social-media {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  margin: 1rem 0;
}
.social-media__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  color: #fff;
  width: 3rem;
  height: 3rem;
  align-self: center;
  border: solid 0.1rem #fff;
  border-radius: 1rem;
  transition: all 0.3s ease;
}
.social-media__icon:nth-child(1):hover {
  background-color: #c32aa3;
  transform: scale(1.2);
  border-color: #c32aa3;
}
.social-media__icon:nth-child(2):hover {
  background-color: #1da1f2;
  transform: scale(1.2);
  border-color: #1da1f2;
}
.social-media__icon:nth-child(3):hover {
  background-color: #1877f2;
  transform: scale(1.2);
  border-color: #1877f2;
}
.social-media__icon:nth-child(4):hover {
  background-color: #000000;
  transform: scale(1.2);
  border-color: #000000;
}

.icon-menu {
  position: fixed;
  width: 3rem;
  height: 3rem;
  margin: 1rem;
  padding: 0 0.1rem;
  top: 0;
  left: 0;
  background-color: #040f16;
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  z-index: 100;
}
@media (min-width: 1024px) {
  .icon-menu {
    display: none;
  }
}
.icon-menu__line, .icon-menu__line::before, .icon-menu__line::after {
  position: relative;
  width: 90%;
  height: 0.3rem;
  background-color: #fff;
  border-radius: 5px;
  transition: background, top, transform 0.2s, 0.2s, 0.2s 0s, 0.2s, 0s;
}
.icon-menu__line::after, .icon-menu__line::before {
  content: "";
  position: absolute;
  width: 100%;
  top: 0.8rem;
}
.icon-menu__line::before {
  top: -0.8rem;
}

.active {
  background-color: transparent;
}

.active::after {
  top: 0;
  transform: rotate(45deg);
  transition-delay: 0s, 0s, 0.2s;
}

.active::before {
  top: 0;
  transform: rotate(-45deg);
  transition-delay: 0s, 0s, 0.2s;
}

@media (min-width: 1024px) {
  .about {
    margin-left: 25rem;
  }
}
.about__header {
  background-image: url(../img/header-about.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 20rem;
}
.about__heading {
  width: 30rem;
  margin: 0;
  border-bottom: solid 0.3rem #001242;
  color: #001242;
}
.about__content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.about__image {
  margin: 0 auto;
  border: solid 2rem #fff;
  box-shadow: 1rem 1rem 0.5rem #c1c1c1;
  max-width: 30rem;
}
@media (min-width: 768px) {
  .about__image {
    max-width: 60rem;
    margin: 0 auto;
  }
}
.about__text {
  margin: 3rem;
  color: #000022;
}
@media (min-width: 1024px) {
  .about__text {
    text-align: center;
  }
}

.learning {
  margin: 2rem 3rem;
}
.learning__course {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 2rem;
}
@media (min-width: 1024px) {
  .learning__course:nth-child(2) img {
    grid-row: 1/2;
    grid-column: 2/3;
    width: 100%;
  }
}
.learning__course:last-child {
  margin-bottom: 0;
}
.learning__course::after {
  content: "";
  width: 95%;
  height: 0.2rem;
  background-color: #000022;
  margin: 2rem;
}
@media (min-width: 1024px) {
  .learning__course::after {
    grid-row: 2/3;
    grid-column: 1/3;
  }
}
@media (min-width: 1024px) {
  .learning__course {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.learning__image {
  margin-bottom: 1rem;
}
@media (min-width: 1024px) {
  .learning__image {
    margin: 0 auto;
    width: 150%;
  }
}
.learning__heading {
  font-size: 3rem;
  color: #000022;
  margin: 1rem 0;
}

@media (min-width: 1024px) {
  .skills {
    margin-left: 25rem;
  }
}
.skills__header {
  background-image: url(../img/header-about.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 20rem;
  margin-bottom: 1rem;
}
.skills__heading {
  width: 30rem;
  border-bottom: solid 0.3rem #001242;
  color: #001242;
}
@media (min-width: 768px) {
  .skills__content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0rem;
  }
}

.skill {
  background-color: #001242;
  margin: 1rem;
  padding: 1rem;
  transition: all 0.3s ease-in;
}
.skill__image {
  width: 8rem;
  height: 8rem;
  background-repeat: no-repeat;
  background-size: cover;
  transition: all 0.3s ease-in;
}
.skill__image--html {
  background-image: url(../img/code.png);
}
.skill__image--frameworks {
  background-image: url(../img/framework.png);
}
.skill__image--react {
  background-image: url(../img/app-development.png);
}
.skill__image--feature {
  background-image: url(../img/feature.png);
}
.skill__name {
  margin: 1rem 0;
  color: #fff;
  transition: all 0.3s ease-in;
}
.skill__text {
  color: #fff;
  transition: all 0.3s ease-in;
}
.skill:hover {
  background-color: #c1c1c1c1;
  transform: scale(1.05);
}
.skill:hover h2, .skill:hover p {
  color: #001242;
}
.skill:hover .skill__image--html {
  background-image: url(../img/code-hover.png);
}
.skill:hover .skill__image--frameworks {
  background-image: url(../img/framework-hover.png);
}
.skill:hover .skill__image--react {
  background-image: url(../img/app-development-hover.png);
}
.skill:hover .skill__image--feature {
  background-image: url(../img/feature-hover.png);
}