@use "base/variables" as v;
@use "base/mixins" as m;

.about {
  @include m.desktop {
    margin-left: 25rem;
  }
  &__header {
    background-image: url(../img/header-about.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 20rem;
  }
  &__heading {
    width: 30rem;
    margin: 0;
    border-bottom: solid 0.3rem v.$secundario;
    color: v.$secundario;
  }
  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  &__image {
    margin: 0 auto;
    border: solid 2rem v.$blanco;
    box-shadow: 1rem 1rem 0.5rem #c1c1c1;
    max-width: 30rem;
    @include m.tablet {
      max-width: 60rem;
      margin: 0 auto;
    }
  }
  &__text {
    margin: 3rem;
    color: v.$primario;
    @include m.desktop {
      text-align: center;
    }
  }
}
