@use "base/variables" as v;
@use "base/mixins" as m;

.social-media {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  margin: 1rem 0;
  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
    color: v.$blanco;
    width: 3rem;
    height: 3rem;
    align-self: center;
    border: solid 0.1rem v.$blanco;
    border-radius: 1rem;
    transition: all 0.3s ease;
    &:nth-child(1) {
      &:hover {
        background-color: #c32aa3;
        transform: scale(1.2);
        border-color: #c32aa3;
      }
    }
    &:nth-child(2) {
      &:hover {
        background-color: #1da1f2;
        transform: scale(1.2);
        border-color: #1da1f2;
      }
    }
    &:nth-child(3) {
      &:hover {
        background-color: #1877f2;
        transform: scale(1.2);
        border-color: #1877f2;
      }
    }
    &:nth-child(4) {
      &:hover {
        background-color: #000000;
        transform: scale(1.2);
        border-color: #000000;
      }
    }
  }
}
