@use "base/variables" as v;
@use "base/mixins" as m;

.sidebar {
  position: fixed;
  width: 25rem;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  background-image: v.$gradientToLeft;
  top: 0;
  left: 0;
  transform: translateX(-100%);
  transition: transform 0.3s ease;
  @include m.desktop {
    transform: translateX(0);
  }
  &__header {
    padding-top: 2rem;
  }
  &__logo {
    border-radius: 50%;
    margin-bottom: 2rem;
  }
  &__slogan {
    text-align: center;
    font-family: v.$fuenteCursiva;
    font-size: 2rem;
    color: v.$blanco;
    margin: 0;
  }
  &__footer {
    width: 100%;
    padding: 1rem 0;
    margin: 1rem 0;
  }
}
.open {
  transform: translateX(0);
}
