@use "base/variables" as v;
@use "base/mixins" as m;

.skill {
  background-color: v.$secundario;
  margin: 1rem;
  padding: 1rem;
  transition: all 0.3s ease-in;
  &__image {
    width: 8rem;
    height: 8rem;
    background-repeat: no-repeat;
    background-size: cover;
    transition: all 0.3s ease-in;
    &--html {
      background-image: url(../img/code.png);
    }
    &--frameworks {
      background-image: url(../img/framework.png);
    }
    &--react {
      background-image: url(../img/app-development.png);
    }
    &--feature {
      background-image: url(../img/feature.png);
    }
  }
  &__name {
    margin: 1rem 0;
    color: v.$blanco;
    transition: all 0.3s ease-in;
  }
  &__text {
    color: v.$blanco;
    transition: all 0.3s ease-in;
  }
  &:hover {
    background-color: v.$gris;
    transform: scale(1.05);
  }
  &:hover h2,
  &:hover p {
    color: v.$secundario;
  }
  &:hover .skill__image--html {
    background-image: url(../img/code-hover.png);
  }
  &:hover .skill__image--frameworks {
    background-image: url(../img/framework-hover.png);
  }
  &:hover .skill__image--react {
    background-image: url(../img/app-development-hover.png);
  }
  &:hover .skill__image--feature {
    background-image: url(../img/feature-hover.png);
  }
}
