@use "base/variables" as v;
@use "base/mixins" as m;

.learning {
  margin: 2rem 3rem;
  &__course {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 2rem;
    @include m.desktop {
      &:nth-child(2) {
        img {
          grid-row: 1 / 2;
          grid-column: 2 / 3;
          width: 100%;
        }
      }
    }
    &:last-child {
      margin-bottom: 0;
    }
    &::after {
      content: "";
      width: 95%;
      height: 0.2rem;
      background-color: v.$primario;
      margin: 2rem;
      @include m.desktop {
        grid-row: 2 / 3;
        grid-column: 1 / 3;
      }
    }
    @include m.desktop {
      @include m.grid(2, 2rem);
    }
  }
  &__image {
    margin-bottom: 1rem;
    @include m.desktop {
      margin: 0 auto;
      width: 150%;
    }
  }
  &__heading {
    font-size: 3rem;
    color: v.$primario;
    margin: 1rem 0;
  }
}
