@use "base/variables" as v;
@use "base/mixins" as m;

.icon-menu {
  position: fixed;
  width: 3rem;
  height: 3rem;
  margin: 1rem;
  padding: 0 0.1rem;
  top: 0;
  left: 0;
  background-color: v.$botones;
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  z-index: 100;
  @include m.desktop {
    display: none;
  }
  &__line,
  &__line::before,
  &__line::after {
    position: relative;
    width: 90%;
    height: 0.3rem;
    background-color: v.$blanco;
    border-radius: 5px;
    transition: background, top, transform 0.2s, 0.2s, 0.2s 0s, 0.2s, 0s;
  }
  &__line::after,
  &__line::before {
    content: "";
    position: absolute;
    width: 100%;
    top: 0.8rem;
  }
  &__line::before {
    top: -0.8rem;
  }
}
.active {
  background-color: transparent;
}
.active::after {
  top: 0;
  transform: rotate(45deg);
  transition-delay: 0s, 0s, 0.2s;
}
.active::before {
  top: 0;
  transform: rotate(-45deg);
  transition-delay: 0s, 0s, 0.2s;
}
